import {
  CBadge,
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import EDU_AXIOS from "src/helpers/edu_axios";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import notify from "src/helpers/notify";
import { useHistory } from "react-router-dom";

function teacher_detail(props) {
  let school_id = props.match.params.school_id;
  let teacher_id = props.match.params.teacher_id;
  let [teacher, setTeacher] = useState("");
  let history = useHistory();

  // get teacher detail from server
  useEffect(() => {
    EDU_AXIOS.get(
      `${process.env.REACT_APP_API_PATH}/tm/get-teacher/${teacher_id}`
    )
      .then((resp) => {
        setTeacher(resp.data.data);
      })
      .catch((err) => {
        notify("error", err);
      });
  }, []);
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Teacher Deatail</h5>
              {console.log(teacher)}
            </CCol>
          </CRow>
          <p className="text-muted">Teacher Profile</p>
          <CRow>
            <CCol md="2">
              <CRow>
                <CCol md="12">
                  {teacher.logo ? (
                    <img
                      style={{
                        height: 72,
                        borderRadius: "10%",
                        marginBottom: 10,
                        textAlign: "center",
                      }}
                      src={
                        process.env.REACT_APP_MEDIA_PATH + "/" + teacher.logo
                      }
                    />
                  ) : (
                    <div style={{ textAlign: "center" }}>
                      <FontAwesomeIcon icon={faUser} size="4x" />
                    </div>
                  )}
                </CCol>
              </CRow>
              <CRow className="mt-2">
                <CCol md="12">
                  <div className="d-grid gap-2">
                    <CButton
                      color="dark"
                      onClick={() => {
                        history.push(
                          `/teacher-management/edit-teacher/${school_id}/${teacher_id}`
                        );
                      }}
                    >
                      Edit Profile
                    </CButton>
                  </div>
                </CCol>
              </CRow>
              ``
            </CCol>
            <CCol md="5">
              <CRow>
                <CCol md="12" className="pb-2">
                  <h6>Basic Information</h6>
                </CCol>
                <CCol md="12">
                  <h5>{teacher.name}</h5>
                  {teacher.status == "active" ? (
                    <CBadge color="success">Active</CBadge>
                  ) : (
                    <CBadge color="warning">Blocked</CBadge>
                  )}
                </CCol>
                <CCol md="12">
                  <b>Pan:</b> {teacher.pan}
                </CCol>
                <CCol md="12">
                  <b>Aadhar:</b> {teacher.aadhar}
                </CCol>
              </CRow>
            </CCol>
            <CCol md="5">
              <CRow>
                <CCol md="12" className="pb-2">
                  <h6>Contact Information</h6>
                </CCol>
                <CCol md="12">
                  <b>Email:</b> {teacher.email}
                </CCol>
                <CCol md="12">
                  <b>Mobile:</b> {teacher.mobile}
                </CCol>
                <CCol md="12">
                  <b>Emg. Mobile:</b> {teacher.emg_no}
                </CCol>
                <CCol md="12">
                  <b>Address:</b> {teacher.address}
                </CCol>
              </CRow>
            </CCol>
          </CRow>
          <CRow className="mt-4">
            <CCol md="2"></CCol>
            <CCol md="10">
              <CRow>
                <CCol md="12" className="pb-2">
                  <h6>Professional Information</h6>
                </CCol>
                <CCol md="12">
                  <b>Primary Subject:</b> {teacher.primary_sub_name}
                </CCol>
                <CCol md="12">
                  <b>Secondary Subject:</b> {teacher.secondary_sub_name}
                </CCol>
              </CRow>
            </CCol>
          </CRow>
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
}

export default teacher_detail;
